import React, { useEffect, useState  } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  BarChart as BarChartIcon,
  Settings as SettingsIcon,
  User as UserIcon,
  Users as UsersIcon,
  Power as PowerIcon,
  Edit2 as EditIcon,
  Lock as LockIcon,
  List as ListIcon,
  Book as SchedulingIcon
} from 'react-feather';
import NavItem from './NavItem';
import {getEmployee} from '../../../services/api';


const user = {  
  avatar: '/static/images/avatars/avatar_1.jpg'
};

const managerItems = [
  {
    href: '/app/dashboard',
    icon: BarChartIcon,
    title: 'Dashboard'
  },
  {
    href: '/app/attendence',
    icon: LockIcon,
    title: 'Attendence'
  },
  {
    href: '/app/employees',
    icon: UsersIcon,
    title: 'Employees'
  },
  {
    href: '/app/add-employee',
    icon: UserIcon,
    title: 'Add Employee'
  },
  {
    href: '/app/scheduling',
    icon: SchedulingIcon,
    title: 'Scheduling'
  },
  {
    href: '/app/settings',
    icon: SettingsIcon,
    title: 'Settings'
  },
  {
    href: '/app/settings',
    icon: SettingsIcon,
    title: 'Punch In'
  },
  {
    href: '/app/get-employee',
    icon: EditIcon,
    title: 'Profile'
  },
  {
    href: '/app/approve/leave',
    icon: ListIcon,
    title: 'All Leaves'
  }
];

const employeeItems = [
  {
    href: '/app/dashboard',
    icon: BarChartIcon,
    title: 'Dashboard'
  },
  {
    href: '/app/get-employee',
    icon: EditIcon,
    title: 'Profile'
  },
  {
    href: '/app/leave',
    icon: PowerIcon,
    title: 'Leave'
  },
  {
    href: '/app/get/leave',
    icon: ListIcon,
    title: 'All Leaves'
  }
];

const useStyles = makeStyles(() => ({
  mobileDrawer: {
    width: 256
  },
  desktopDrawer: {
    width: 256,
    top: 64,
    height: 'calc(100% - 64px)'
  },
  avatar: {
    cursor: 'pointer',
    width: 100,
    height: 100
  }
}));


const NavBar = ({ onMobileClose, openMobile }) => {
  const classes = useStyles();
  const location = useLocation();
  const [userName, setUsername] = useState();
  const [role, setRole] = useState();
  const userrole = localStorage.getItem("userrole")

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    getEmployee().then((res) => {
      setUsername(res.data.firstName +" "+ res.data.lastName);
      setRole(res.data.role);
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const content = (
    <Box
      height="100%"
      display="flex"
      flexDirection="column"
    >
      <Box
        alignItems="center"
        display="flex"
        flexDirection="column"
        p={2}
      >
        <Avatar
          className={classes.avatar}
          component={RouterLink}
          src={user.avatar}
          to="/app/account"
        />
        <Typography
          className={classes.name}
          color="textPrimary"
          variant="h5"
        >
          {userName}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {role}
        </Typography>
      </Box>
      <Divider />
      <Box p={2}>
        <List>
          {
          userrole === "Manager" ?
          managerItems.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          )) : 
          employeeItems.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          ))
          }
        </List>
      </Box>
      <Box flexGrow={1} />
      {/* <Box
        p={2}
        m={2}
        bgcolor="background.dark"
      >
        <Typography
          align="center"
          gutterBottom
          variant="h4"
        >
          Need more?
        </Typography>
        <Typography
          align="center"
          variant="body2"
        >
          Upgrade to PRO version and access 20 more screens
        </Typography>
        <Box
          display="flex"
          justifyContent="center"
          mt={2}
        >
          <Button
            color="primary"
            component="a"
            href="https://react-material-kit.devias.io"
            variant="contained"
          >
            See PRO version
          </Button>
        </Box>
      </Box> */}
    </Box>
  );

  return (
    <>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          classes={{ paper: classes.mobileDrawer }}
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden mdDown>
        <Drawer
          anchor="left"
          classes={{ paper: classes.desktopDrawer }}
          open
          variant="persistent"
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

NavBar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

NavBar.defaultProps = {
  onMobileClose: () => {},
  openMobile: false
};

export default NavBar;
