import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  makeStyles,
  Button
} from '@material-ui/core';
import getInitials from '../../../utility/getInitials';
import ScheduleDilougForm from './ScheduleDilougForm';

const useStyles = makeStyles((theme) => ({
  root: {},
  avatar: {
    marginRight: theme.spacing(2)
  }
}));


const Results = ({ className, date, employees, ...rest }) => {
  const classes = useStyles();

  const monday = date;
  const tuesday = date + 1 * 24 * 60 * 60 * 1000;
  const wednesday = date + 2 * 24 * 60 * 60 * 1000;
  const thursday = date + 3 * 24 * 60 * 60 * 1000;
  const friday = date + 4 * 24 * 60 * 60 * 1000;
  const satday = date + 5 * 24 * 60 * 60 * 1000;
  const sunday = date + 6 * 24 * 60 * 60 * 1000;

  function displayDate(date){
    return new Date(date).toString().slice(0,10);
  }

  const getTimeEntries = (entries) => {
    console.log(entries);
  }


  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <PerfectScrollbar>
        <Box minWidth={1050}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Name
                </TableCell>
                <TableCell>
                  {displayDate(monday)}
                </TableCell>
                <TableCell>
                  {displayDate(tuesday)}
                </TableCell>
                <TableCell>
                  {displayDate(wednesday)}
                </TableCell>
                <TableCell>
                  {displayDate(thursday)}
                </TableCell>
                <TableCell>
                  {displayDate(friday)}
                </TableCell>
                <TableCell>
                  {displayDate(satday)}
                </TableCell>
                <TableCell>
                  {displayDate(sunday)}
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {employees.map((employee) => (
                <TableRow
                  hover
                  key={employee.id}
                >
                  <TableCell>
                    <Box
                      alignItems="center"
                      display="flex"
                    >
                      <Typography
                        color="textPrimary"
                        variant="body1"
                      >
                        {employee.name}
                      </Typography>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <ScheduleDilougForm id={employee.id} date={monday} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} date={tuesday} />
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} date={wednesday} />
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} date={thursday} />
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} date={friday} />
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} date={satday} />
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} date={sunday} />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  employees: PropTypes.array.isRequired
};

export default Results;
